// Modules
import ReactDOM from 'react-dom';
import React, { Component } from 'react';



// Pictures

import item1 from './img/goods/Item1.png';
import item2 from './img/goods/Item2.png';
import item3 from './img/goods/Item3.png';
import item4 from './img/goods/Item4.png';


// Style

import './calc.css';


// SCRIPTS

const Calculator = () =>{

		const calc1 = () => {

			let Purchase, Discount, Name;

			Name = document.getElementById('Name1').innerText;
			Purchase = document.getElementById('purchase1').innerText;
			Discount = document.getElementById('discount1').innerText;

			Purchase = parseInt(Purchase);
			Discount = parseInt(Discount)/100;

			Discount = Purchase * Discount;
			let cost;


			cost = Purchase - Discount;
			cost = parseInt(cost);

			document.getElementById('name').innerHTML = Name;
			document.getElementById('Purchase').innerHTML = Purchase + ' rub';
			document.getElementById('Discount').innerHTML = Discount + ' rub';
			document.getElementById('cost').innerHTML = cost + ' rub';
		}

		const calc2 = () => {

			let Purchase, Discount, Name;

			Name = document.getElementById('Name2').innerText;
			Purchase = document.getElementById('purchase2').innerText;
			Discount = document.getElementById('discount2').innerText;

			Purchase = parseInt(Purchase);
			Discount = parseInt(Discount)/100;

			Discount = Purchase * Discount;
			let cost;


			cost = Purchase - Discount;
			cost = parseInt(cost);

			document.getElementById('name').innerHTML = Name;
			document.getElementById('Purchase').innerHTML = Purchase + ' rub';
			document.getElementById('Discount').innerHTML = Discount + ' rub';
			document.getElementById('cost').innerHTML = cost + ' rub';
		}

		const calc3 = () => {

			let Purchase, Discount, Name;

			Name = document.getElementById('Name3').innerText;
			Purchase = document.getElementById('purchase3').innerText;
			Discount = document.getElementById('discount3').innerText;

			Purchase = parseInt(Purchase);
			Discount = parseInt(Discount)/100;

			Discount = Purchase * Discount;
			let cost;


			cost = Purchase - Discount;
			cost = parseInt(cost);

			document.getElementById('name').innerHTML = Name;
			document.getElementById('Purchase').innerHTML = Purchase + ' rub';
			document.getElementById('Discount').innerHTML = Discount + ' rub';
			document.getElementById('cost').innerHTML = cost + ' rub';
		}
		
		const calc4 = () => {

			let Purchase, Discount, Name;

			Name = document.getElementById('Name4').innerText;
			Purchase = document.getElementById('purchase4').innerText;
			Discount = document.getElementById('discount4').innerText;

			Purchase = parseInt(Purchase);
			Discount = parseInt(Discount)/100;

			Discount = Purchase * Discount;
			let cost;


			cost = Purchase - Discount;
			cost = parseInt(cost);

			document.getElementById('name').innerHTML = Name;
			document.getElementById('Purchase').innerHTML = Purchase + ' rub';
			document.getElementById('Discount').innerHTML = Discount + ' rub';
			document.getElementById('cost').innerHTML = cost + ' rub';
		}

//  END SECTION WITH SCRIPTS

// JSX code

		return (		
			<section className="shopCalc">
				<h3>Calculator for goods</h3>
				<div className="FieldForGoods">
					<div className="calculator">
						<p className="BlockForCalc">Name: <label id="name"></label></p>
						<p className="BlockForCalc">Price: <label id="Purchase"></label></p>
						<p className="BlockForCalc">Discount: <label id="Discount"></label></p>
						<div className="answerCalc">
							<p>Cost: <label id="cost"></label></p>
						</div>
					</div>
					<div className="Inner_FieldForGoods">
						<div className="BlockForGood"> 
							<div onClick={calc1} id="BlockForGood1" className="forCharacteristics">
								<p>Name: <label id="Name1">OAS Value line</label></p>
								<p>Price: <label id="purchase1">7150</label> rub</p>
								<p>Discount: <label id="discount1">30</label>%</p>
							</div>
							<img className="item1 itemForCalc" src={item1}/>
						</div>
						<div className="BlockForGood"> 
							<div onClick={calc2} id="BlockForGood2" className="forCharacteristics">
								<p>Name: <label id="Name2">ACER V196HQLAb</label></p>
								<p>Price: <label id="purchase2">8340</label> rub</p>
								<p>Discount: <label id="discount2">5</label>%</p>
							</div>
							<img className="item2 itemForCalc" src={item2}/>
						</div>
						<div className="BlockForGood"> 
							<div onClick={calc3} id="BlockForGood3" className="forCharacteristics">
								<p>Name: <label id="Name3">ACER K192HQLb</label></p>
								<p>Price: <label id="purchase3">12250</label> rub</p>
								<p>Discount: <label id="discount3">6</label>%</p>
							</div>
							<img className="item3 itemForCalc" src={item3}/>
						</div>
						<div className="BlockForGood"> 
							<div onClick={calc4} id="BlockForGood4" className="forCharacteristics">
								<p>Name: <label id="Name4">PHILIPS 193V5LSB2</label></p>
								<p>Price: <label id="purchase4">5999</label> rub</p>
								<p>Discount: <label id="discount4">12</label>%</p>
							</div>
							<img className="item4 itemForCalc" src={item4}/>
						</div>
					</div>
				</div>
			</section>
		);
}

// End of JSX code


// Export file
export default Calculator;