//  Import Scripts and important elements with modules components, and classes

import ReactDOM from 'react-dom';
import React, { Component } from 'react';
import './app.css';
import html from './img/html.png';
import css from './img/css.png';
import js from './img/JavaScript.png';
import react from './img/React.png';


//  Import Pictures
import OctoTrade from './img/PicturesForExample/OctoTrade.png';
import OctoTrade1 from './img/PicturesForExample/OctoTrade1.png';
import Business from './img/PicturesForExample/Business.png';


//  Import JavaScript example

import Calculator from './Calculator.js';


//  Import Sliders elements and CSS styles for second example

import './slider.css';

import ItIs from './img/Slider/pic2.jpg';
import Very from './img/Slider/pic3.jpg';
import Cool from './img/Slider/pic4.jpg';


import HTML from './HTML.js';
import CSS from './CSS.js';
import JS from './JS.js';
import JQ from './JQ.js';
import R from './R.js';
import Connect from './Connect.js';


//  SCRIPTS


var itIs = {
  width: "25%",
  height: "350px",
  backgroundImage: `url(${ItIs})`,
  backgroundSize: 'cover'
};

var very = {
  width: "25%",
  height: "350px",
  backgroundImage: `url(${Very})`,
  backgroundSize: 'cover'
};

var cool = {
  width: "25%",
  height: "350px",
  backgroundImage: `url(${Cool})`,
  backgroundSize: 'cover'
};


// EVENTS FOR CLOSE BUTTONS

function click1() {
    document.querySelector('#ItemHtml').classList.remove('Show_Block');
    document.querySelector('.skills').classList.remove('moveLeft');
    document.querySelector('.ToBack1').style.display = 'none';
    document.querySelector('.Text1').style.display = "none";
    document.querySelector('.ConnectsField').style.display = "flex";
}

function click2() {
    document.querySelector('#ItemCSS').classList.remove('Show_Block');
    document.querySelector('.skills').classList.remove('moveLeft');
    document.querySelector('.ToBack2').style.display = 'none';
    document.querySelector('.Text2').style.display = "none";
    document.querySelector('.ConnectsField').style.display = "flex";
}

function click3() {
    document.querySelector('#ItemJS').classList.remove('Show_Block');
    document.querySelector('.skills').classList.remove('moveLeft');
    document.querySelector('.ToBack3').style.display = 'none';
    document.querySelector('.Text3').style.display = "none";
    document.querySelector('.ConnectsField').style.display = "flex";
}

function click4() {
    document.querySelector('#ItemR').classList.remove('Show_Block');
    document.querySelector('.skills').classList.remove('moveLeft');
    document.querySelector('.ToBack5').style.display = 'none';
    document.querySelector('.Text5').style.display = "none";
    document.querySelector('.ConnectsField').style.display = "flex";
}

// END of SCRIPTS


// CODE START

ReactDOM.render(


	<div className="wrapper">
		<div className="inner-wrapper">

			<div className="about"> 
				Hello, my name is Danil, I'm front-end developer.
			</div>

			<div to={""} className="MySkills">My skills</div>

			<div className="flow">

				<div className="Cursor"></div>

			</div>

			<div className="Connect">
				<Connect/>
			</div>

			<div id="ItemHtml" className="Text1">
				
				<section >

					<h3>Simple lending made on HTML5 and CSS3</h3>
						<div className="blockForPic">
							<img src={OctoTrade} className="picture"/>
							<img src={OctoTrade1} className="picture"/>
						</div>
					<div> 
						<p>Hourage: about 4 hours</p>
						<p className="ForLink">Link on the site: <a target="_blanck" href="https://porfolion.000webhostapp.com/">OctoTrade</a></p>
					</div>

				</section>

				<button onClick={click1} className="ToBack1" type="confirm">Go back</button>

			</div>

			<div id="ItemCSS" className="Text2">
				
				<section className="Flex">

				<h3>Nice slider on FlexBox</h3>

					<div className="Block_Flex">

						<div className="slider">

							<div style={itIs} className="item item-2"><span>Summer</span></div>
							<div style={very} className="item item-3"><span>It is</span></div>
							<div style={cool} className="item item-4"><span>Cool</span></div>

						</div>

					</div>

				</section>
				<button onClick={click2} className="ToBack2" type="confirm">Go back</button>

			</div>

			<div id="ItemJS" className="Text3">
				<Calculator/>
				<button onClick={click3} className="ToBack3" type="confirm">Go back</button>
			</div>

			<div id="ItemR" className="Text5">

				<section >
					<h3>React</h3>
					<p>This site was created with this library</p>
				</section>

				<button onClick={click4} className="ToBack5" type="confirm">Go back</button>

			</div>

			<div className="skills">

				<div id="skill">
					<HTML class="Block"/>
					<img id="pic" src={html} alt="HTML"/>
				</div>

				<div id="skill">
					<CSS class="Block"/>
					<img id="pic" src={css} alt="CSS"/>
				</div>

				<div id="skill">
					<JS class="Block"/>
					<img id="pic" src={js} alt="JS"/>
				</div>

				<div id="skill">
					<R class="Block"/>
					<img id="pic" src={react} alt="React"/>
				</div>

			</div>
		</div>
	</div>,

	document.getElementById('app'),

)

//  CODE END
