//  MODULE
import React from 'react';

// Import style
import './Connect.css';
import './app.css';
import './script.js';

//  Connect COMPONENT

const Connect = () => {

	const globalObj = {
		theme: '',
		name: '',
		message: ''
	}

	const openConnect = () => {
    document.querySelector('#hiddenCon').classList.add('#open');
    document.querySelector('.HideBlockWithForm').style.display = "block";
    document.querySelector('#hiddenCon').style.display = "block";
    document.querySelector('.skills').style.display = "none";
    document.querySelector('.ConnectsField').style.display = "none";
    document.querySelector('.about').style.display = "none";
    document.querySelector('.MySkills').style.display = "none";
    document.querySelector('.flow').style.display = "none";
    document.querySelector('.butForSend').style.display = "block";
    document.querySelector('#SendArea').style.display = "block";
	}

	const back = () => {
    document.querySelector('.HideBlockWithForm').style.display = "none";
    document.querySelector('#hiddenCon').style.display = "none";
    document.querySelector('.skills').style.display = 'flex';
    document.querySelector('.ConnectsField').style.display = "flex";
    document.querySelector('.about').style.display = "block";
    document.querySelector('.MySkills').style.display = "block";
    document.querySelector('.flow').style.display = "block";
    document.querySelector('#SendArea').style.display = "none";
}

const message = (event) => {
	event.preventDefault();
	let Name, theme, text;

	

	Name = document.getElementById('SendFormName').value;
	theme = document.getElementById('SendFormTheme').value;
	text = document.getElementById('FormArea').value;
	if ((Name && theme && text) != '' ) {
	document.getElementById('SendArea').innerHTML = `
		<div id="BlockWithMessage">
			<p id="titleMessage"></p>
			<p id="Name"></p>
			<pre id="message"></pre>
		</div>
	`
	document.getElementById('message').innerHTML = text;
	document.getElementById('titleMessage').innerHTML = theme;
	document.getElementById('Name').innerHTML = Name;
} else {
	alert('Fill all fields');
}
}

const change1 = () => {

}

const change2 = () => {
	
}

const change3 = () => {
	
}


const clear = () => {
	document.querySelector('#FormArea').innerHTML = '';
}


  return (
  	<div>
	    <div onClick={openConnect} className="ConnectsField">
	      Connect with me
	    </div>   
	    <div id="hiddenCon">
	    	<form  method="post" action="./php/api.php"> 
	    		<input
	    			onKeyDown={change1}
	    			id="SendFormName"
	    			type="text"
	    			name="name"
	    			placeholder="Your name"
	    		/>
	    		<input
	    			onKeyDown={change2}
	    			id="SendFormTheme"
	    			type="text"
	    			name="Theme"
	    			placeholder="Theme"
	    		/>
	    		<input
	    			onKeyDown={change2}
	    			id="SendFormPhone"
	    			type="phone"
	    			name="number"
	    			placeholder="Phome number"
	    		/>
	    		<textArea
	    			onKeyDown={change3}
	    			id="FormArea"
	    			type="textArea"
	    			name="Text"
	    			placeholder="Your offer"
	    		/>
	    		<button value="OK" onClick={message} className="butForSend">Send</button>
	    	</form>
	    </div>  
	    <div id="SendArea">

	    </div>
	    <button onClick={back} className="HideBlockWithForm" type="confirm">Go back</button>	
    </div>
  );
}

// END Connect COMPONENT

export default Connect;