window.onload = function() {

	const inp_name = document.querySelector('input[name=name]');
	const theme = document.querySelector('input[name=Theme]');
	const text = document.querySelector('input[name=Text]');

	document.querySelector('.butForSend').onclick = function () {
		const params = 'name=' + inp_name.value;
		ajaxPost(params);
	}
}

function ajaxPost(params, callBack) {
	const f = callBack || function(data) {

	}

	const request = new XMLHttpRequest();

	request.onreadystatechange = function() {
		if (request.readyState == 4 && request.status == 200) {
			f(request.responseText);
		}
	}

	request.open('POST', 'php/api.php');
	request.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
	request.send(params);
}