//  MODULE
import React from 'react';

//  CSS COMPONENT

class CSS extends React.Component {
  render() {
    return (
      <div onClick={
            function () {
              document.querySelector('#ItemCSS').classList.add('Show_Block');
              document.querySelector('.skills').classList.add('moveLeft');
              document.querySelector('.ToBack2').style.display = "block";
              document.querySelector('.Text2').style.display = "block";
              document.querySelector('.ConnectsField').style.display = 'none';
            }} className="TextWithSkill">
        <div className="Name">CSS</div>
        </div>	
    );
  }
}

// END CSS COMPONENT

export default CSS;