//  MODULE
import React from 'react';


//  HTML COMPONENT

class HTML extends React.Component {
  render() {
    return (
      <div onClick={
            function () {
              document.querySelector('#ItemHtml').classList.add('Show_Block');
              document.querySelector('.skills').classList.add('moveLeft');
              document.querySelector('.ToBack1').style.display = "block";
              document.querySelector('.Text1').style.display = "block";
              document.querySelector('.ConnectsField').style.display = 'none';
            }} className="TextWithSkill">
        <div className="Name">HTML</div>
      </div>      	
    );
  }
}

// END HTML COMPONENT

export default HTML;