import React from 'react';

class JS extends React.Component {
  render() {
    return (
      <div onClick={
            function () {
              document.querySelector('#ItemJS').classList.add('Show_Block');
              document.querySelector('.skills').classList.add('moveLeft');
              document.querySelector('.ToBack3').style.display = "block";
              document.querySelector('.Text3').style.display = "block";
              document.querySelector('.ConnectsField').style.display = 'none';
            }} className="TextWithSkill">
        <div className="Name">JavaScript</div>
      </div> 
    );
  }
}

export default JS;