// MODULE
import React from 'react';


// REACT COMPONENT

class R extends React.Component {
  render() {
    return (
      <div onClick={
            function () {
              document.querySelector('#ItemR').classList.add('Show_Block');
              document.querySelector('.skills').classList.add('moveLeft');
              document.querySelector('.ToBack5').style.display = "block";
              document.querySelector('.Text5').style.display = "block";
              document.querySelector('.ConnectsField').style.display = 'none';
            }} className="TextWithSkill">
        <div className="Name">React</div>
      </div> 
    );
  }
}

//  END REACT COMPONENT

export default R;